import React, { Component } from 'react';
import {Route, BrowserRouter as Router, Switch, Redirect, HashRouter} from 'react-router-dom';
import ScrollToTop from './scrollToTop';
import Designing from '../Pages/designing';
import SideBar from './sideBar/sideBar';
import Software from '../Pages/software';
import Programming from '../Pages/programing';
import Web from '../Pages/web';
import Databases from '../Pages/databases';
import ML from '../Pages/ml';
import UX_UI from '../Pages/uxui';
import Graphic from '../Pages/graphic';


class Skills extends Component {
    state = {  }


    
    render() { 
        return ( 
            <HashRouter>
                <ScrollToTop/>
                <div className="skills-page">
                    <SideBar/>     
                    <section className="fragment">
                        <Switch>
                            <Route path="/Portfolio" exact>
                                <Redirect to="/Portfolio/programming" />
                            </Route>
                            <Route path='/Portfolio/programming' component={Programming} /> 
                            <Route path='/software'  component={Software} />
                            <Route path='/web'  component={Web} />
                            <Route path='/database'  component={Databases} />
                            <Route path='/ml'  component={ML} />

                            <Route path='/designing'  component={Designing} />
                            <Route path='/ux_ui'  component={UX_UI} />
                            <Route path='/graphic'  component={Graphic} />
                            
                        </Switch>
                    </section >
                </div>
            </HashRouter>
        );
    }
}
 
export default Skills;